
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from "axios";
import api from "../api";
import util from "../util"

export default {
components: {
PopupPicker,
Group,
XButton
},
data() {
return {
school: [""],
school_title: "",
school_list: [["", ""]], //
frequency: [""],
frequency_title: "",
frequency_list: [["", "", ""]],
type: [""],
type_title: "",
type_list: [["", "", "", ""]]
};
},

methods: {
get_user_init_setting() {
let _this = this;
axios
.get(api.get_user_init_setting)
.then(function(response) {
console.log(response.data);
_this.school = [response.data.data.school];

if(response.data.data.frequency == 1){
console.log('_this.frequency[0]' + _this.frequency[0])
console.log('')
_this.frequency = [""]
}
if(response.data.data.frequency == 3){
console.log('_this.frequency[0]' + _this.frequency[0])
console.log('')
_this.frequency = [""]
}
if(response.data.data.frequency == 7){
console.log('_this.frequency[0]' + _this.frequency[0])
console.log('')
_this.frequency[0] = [""]
}
_this.type = [response.data.data.type];
})
.catch(function(error) {
console.log('')
console.log(error);
});
console.log('axiosthis.frequency')
console.log(this.frequency);
},
set_user_setting() {
let temp_frequency;
if(this.frequency[0] == ''){
temp_frequency = 1;
}
if(this.frequency[0] == ''){
temp_frequency = 3;
}
if(this.frequency[0] == ''){
temp_frequency = 7;
}
let _this = this;
console.log('')
axios
.post(api.set_user_setting, {
school: _this.school[0],
frequency: temp_frequency,
type:_this.type[0]
})
.then(function(response) {
console.log('')
_this.get_user_init_setting();
console.log(response);
})
.catch(function(error) {
console.log(error);
});
}

},
mounted: function() {
this.get_user_init_setting();
},
created:function(){
util.getCode('setting');
}
};



